import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  { path: '/', name:'products', component: () => import('../components/products/products-list')},
  {path:'/paymentCondition', name:'condition', component:() => import('../components/products/payment-condition')}
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
